$main: #F3A846;
.container{
    max-width: 400px;
    margin: 0px auto;
    padding-left: 20px;
    padding-right: 20px;
}
body{
    margin: 0;
}
h1{
    font-family: modesto-text, serif;
    font-size: 50px;
}
p{
    font-family: source-code-pro, monospace;
    font-size: 25px;
    color:#333;
}
a{
    font-weight: 600;
    color:$main;
    text-decoration: none;
}
img{
    width: 100%;
}
.intro{
    background: $main;
    padding: 50px 0 150px;
    text-align: center;
    color:white;
    position: relative;
    p{
        margin: 0;
        color: white;
    }
    h1{
        margin-top: 10px;
    }
    img{
        max-width: 300px;
        position: absolute;
        margin-left: auto;
        margin-right: auto;
        left: 0;
        right: 0;
        bottom: 0;
        transform: translateY(50%);
    }
}
.main{
    margin-top: 200px;
    p{
        text-align: center;
    }
}
.inputText{
    border: 3px solid $main;
    border-radius: 10px;
    min-height: 200px;
    p{
        text-align: left;
        margin: 0;
        padding: 20px;
        font-family: modesto-text, serif;
        line-height: 38px;
    }
}
.bottom{
    padding: 100px 0 25px;
    p{
        font-size: 15px;
        text-align: center;
    }
}
@media only screen and (max-width: 600px) {
    p{
        font-size: 20px;
    }
    .intro img{
        width: 80%;
    }
}
